import { useState } from 'react'
import { quotesDb } from '~/utils/quotesDb'
import { useLoaderData } from '@remix-run/react'


export const loader = async () => {
  return { quotesDb }
}


export default function QuoteCard() {
  const quotes = useLoaderData()
  const [index, setIndex] = useState()

  const randomIndex = () => {
    const index = Math.floor(Math.random() * quotes.length)
    setIndex(index)
  }



  return (
    <div id='quote-box' className='quote-box'>
      <h1>Here's your random quote</h1>
      <div id='text' className='text'>{quotes[index] && quotes[index].quoteText}</div>
      <div id='author' className='author'>{quotes[index] && quotes[index].author}</div>
      <button id='new-quote' className='new-quote' onClick={randomIndex}>New Quote</button>
      <a id='tweet-quote' href="twitter.com/intent/tweet" className='tweet-quote'>Tweet Quote</a>
    </div>
  );
}


